html, body {
    height: 100%;
    margin: 0;
  }

  .blog-section {
    background: linear-gradient(135deg, #1f1f1f, #3a3a3a); 
    padding: 2rem;
    border-radius: 10px;
    height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-attachment: fixed; 
    background-position: center;
    background-size: cover;
    overflow: hidden;
  }
  
  .blog-section__title {
    text-align: center;
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .blog-section__construction-message {
    background-color: rgba(0, 0, 0, 0.6); 
    padding: 3rem;
    border-radius: 10px;
    text-align: center;
  }
  
  .blog-section__construction-title {
    font-size: 2.5rem;
    color: #ffcc00;
    margin-bottom: 1rem;
  }
  
  .blog-section__construction-text {
    font-size: 1.25rem;
    color: #f0f0f0;
  }
  